@import url(https://fonts.googleapis.com/css2?family=Hind:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media only screen and (max-width: 600px) {
  form {
    max-width: 90vw !important;
  }
}
  form {
    max-width: 43vw;
    margin: 0 auto;
    padding: 2rem;
   
  }
  .form-section li{
    list-style: none;
  }
  
  h1 {
    font-weight: 100;
    color: rgb(0, 0, 0);
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(79, 98, 148);
  }
  
  input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    border-radius: 4px;
    border: 1px solid rgb(140, 167, 241);
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 14px;
  }
  textarea {
    border: 1px solid rgb(140, 167, 241);

  }
.addmore-button{
  background-color: #0045ad;
  color: white;
  font-weight: 900;
}
  label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 20px;
    color: #333333;
    font-size: 14px;
    font-weight: 400;
  }
  
  button[type="submit"],
  input[type="submit"] {
    background: #535ef5!important;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    border: none;
    margin-top: 40px;
    padding: 20px;
    font-size: 16px;
    font-weight: 800;
    letter-spacing: 3px;
  }
  
  button[type="submit"]:hover,
  input[type="submit"]:hover {
    background: #bf1650;
  }
  
  button[type="submit"]:active,
  input[type="button"]:active,
  input[type="submit"]:active {
    transition: 0.3s all;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    border: 1px solid transparent;
    opacity: 0.8;
  }
  
  input:disabled {
    opacity: 0.4;
  }
  
  input[type="button"]:hover {
    transition: 0.3s all;
  }
  
  button[type="submit"],
  input[type="button"],
  input[type="submit"] {
    -webkit-appearance: none;
  }
  
  pre {
    color: white;
  }
  
  hr {
    margin-top: 30px;
  }
  
  .addmore-button,.delete-item {
    display: block;
    -webkit-appearance: none;
            appearance: none;
    margin-top: 40px;
    border: 1px solid #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
    width: 120px;
  }

  
  .form-section{
      margin-top:  2.5rem;
      background: rgba( 255, 255, 255, 0.25 );
      box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.06 );
      backdrop-filter: blur( 4px );
      -webkit-backdrop-filter: blur( 4px );
      border-radius: 10px;
      /* border: 1px solid rgba( 255, 255, 255, 0.18 ); */
      padding: 2rem;

  }
  .form-error{
    color: #e90013;
    display: flex;
    font-size: .8rem;
    letter-spacing: .5px;  }

    .form-bio textarea{ 
        width: 94%;
        height: 10vw;
        padding:1rem;
    }
    .delete-item{
        background-color: #e90013;
        color:white;
        border: none; 
        font-weight: 800;
    }
    .form-skills{
        padding-bottom: 5rem;
    }
